<template>
  <div style="background:#424242; margin-bottom:40px;">
    <!-- <h1>{{title}}</h1> -->
    <!-- <p>{{nameOfChild2}}</p>
    <p>{{sayHello}}</p> -->


    <v-layout wrap>
      <v-flex
      pa-1
      >
      <v-card
      dark
      color="orange darken-2"
      >



      <span>
        <h2 style="margin-top:0px;">오늘 읽은 책</h2>
        <div class="input-group" style="margin-bottom:10px; width:50%;">
          <input type="text" class="form-control"
          placeholder="책 제목을 입력하세요."
          v-model="name"
          v-on:keyup.enter="createTodo(name)">
          <span class="input-group-btn" >
            <button class="btn btn-default" type="button"
            @click="createTodo(name)">추가</button>
          </span>
        </div>
        <ul class="list-group" style = "width:50%">
          <li class="list-group-item" v-for="(todo, index) in todos">
            {{todo.name}}
            <div class="btn-group pull-right"
            style="font-size: 12px; line-height: 1;">
            <button type="button"
            class="btn-link dropdown-toggle"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false">
            <span class="caret"></span>
          </button>
          <ul class="dropdown-menu">
            <li>
              <a href="#" @click="deleteTodo(index)">삭제</a>
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </span>




</v-card>
</v-flex>
<v-flex
pa-1
>
<v-card
dark
color="green darken-1"
>
<span>


  <v-layout>
    <v-flex>
      <v-sheet height="500">
        <v-calendar
        :now="today"
        :value="today"
        color="primary"
        >
        <template v-slot:day="{ present, past, date }">
          <v-layout
          fill-height
          >
          <template v-if="past && tracked[date]">
            <v-sheet
            v-for="(percent, i) in tracked[date]"
            :key="i"
            :title="category[i]"
            :color="colors[i]"
            :width="`${percent}%`"
            height="100%"
            tile
            ></v-sheet>
          </template>
        </v-layout>
      </template>
    </v-calendar>
  </v-sheet>
</v-flex>
</v-layout>
</span>




</v-card>
</v-flex>
</v-layout>



</div>
</template>

<script>
export default {
  // props: ['nameOfChild2'],
  props: {
    nameOfChild2:{
      type: String,
      default: "독"
    }

  },
  computed: {
    sayHello () {
      return "[Making] " + this.nameOfChild2
    }
  },
  data() {
    return {
      // title: "안녕!"
      todos: [],
      search: '',
      today: '2019-07-16',
      tracked: {
        '2019-07-11': [10, 10, 10],
        '2019-07-09': [10],
        '2019-07-07': [0, 78, 5],
        '2019-07-06': [0, 0, 50],
        '2019-07-05': [0, 10, 23],
        '2019-07-04': [2, 90],
        '2019-07-03': [10, 32],
        '2019-07-02': [80, 10, 10],
        '2019-07-01': [20, 25, 10],
      },
      colors: ['#424242', '#fb8c00', '#000000'],
      category: ['Development', 'Meetings', 'Slacking'],

    }
  },
  methods:{
    deleteTodo(i){
      this.todos.splice(i,1);
    },
    createTodo(name){
      if(name != null){
        this.todos.push({name:name});
        this.name = null
      }
    }
  },

}
</script>

<style scoped>
.theme--dark.v-sheet {
  color: #424242;
}



</style>
