ets. Libraries have had at least to comprehend, if not become a key node within, evolving systems of media production and distribution. Consider the medieval scriptoria where manuscripts were produced; the evolution of the publishing industry and book trade after Gutenberg; the rise of information technology and its webs of wires, protocols and regulations. 1 At every stage, the contexts — spatial, political, economic, cultural — in which libraries function have shifted; so they are continuously reinventing themselves and the means by which they provide those vital information services.\n        ")]),_c('p',[_vm._v("\n          Libraries have also assumed a host of ever-changing social and symbolic functions. They have been expected to symbolize the eminence of a ruler or state, to integrally link “knowledge” and “power” — and, more recently, to serve as “community centers,” “public squares” or “think tanks.” Even those seemingly modern metaphors have deep histories. The ancient Library of Alexandria was a prototypical think tank, 2 and the early Carnegie buildings of the 1880s were community centers with swimming pools and public baths, bowling alleys, billiard rooms, even rifle ranges, as well as book stacks. 3 As the Carnegie funding program expanded internationally — to more than 2,500 libraries worldwide — secretary James Bertram standardized the design in his 1911 pamphlet “Notes on the Erection of Library Buildings,” which offered grantees a choice of six models, believed to be the work of architect Edward Tilton. Notably, they all included a lecture room.\n        ")]),_c('p',{staticClass:"mb-0"},[_vm._v("\n          In short, the library has always been a place where informational and social infrastructures intersect within a physical infrastructure that (ideally) supports that program.\n        ")])])],1)],1),_c('v-tab-item',[_c('v-card',{staticStyle:{"background":"coral"},attrs:{"flat":""}},[_c('v-card-title',{staticClass:"headline"},[_vm._v("Best Seller")]),_c('v-card-text',[_c('p',[_vm._v("\n          According to Guinness World Records as of 1995, the Bible is the best-selling book of all time, with an estimated 5 billion copies sold and distributed.[1] The Qur'an is also widely reported to be one of the most printed and distributed books worldwide.[2][3][4] The Quotations from Chairman Mao Tse-tung, also known as the Little Red Book, has produced a wide array of sales and distribution figures — with estimates ranging from 800 million[1][2][4] to over 6.5 billion printed volumes,[5] with some claiming the distribution ran into the \"billions\"[6] and some citing \"over a billion\" official volumes between 1966 and 1969 alone as well as \"untold numbers of unofficial local reprints and unofficial translations.\"[7][8] Exact print figures for these and other books may also be missing or unreliable since these kinds of books may be produced by many different and unrelated publishers, in some cases over many centuries. All books of a religious, ideological, philosophical or political nature have thus been excluded from the below lists of best-selling books for these reasons.\n        ")]),_c('p',[_vm._v("\n          Having sold more than 500 million copies worldwide,[9][10][11] Harry Potter by J. K. Rowling is the best-selling book series in history. The first novel in the series, Harry Potter and the Philosopher's Stone, has sold in excess of 120 million copies,[12] making it one of the best-selling books of all time. As of June 2017, the series has been translated into 80 languages,[13] placing Harry Potter among history's most translated literary works. The last four books in the series consecutively set records as the fastest-selling books of all time, where the final installment, Harry Potter and the Deathly Hallows, sold roughly fifteen million copies worldwide within twenty-four hours of its release.[14][15] With twelve million books printed in the first U.S. run, it also holds the record for the highest initial print run for any book in history.[16][17]\n        ")]),_c('p',{staticClass:"mb-0"},[_vm._v("\n          This page was last edited on 20 July 2019, at 15:35 (UTC).\n        ")])])],1)],1),_c('v-tab-item',[_c('v-card',{staticStyle:{"background":"coral"},attrs:{"flat":""}},[_c('v-card-title',{staticClass:"headline"},[_vm._v("곰돌이 푸, 행복한 일은 매일 있어 읽고나서")]),_c('v-card-text',[_c('p',[_vm._v("\n          나이가 들었다고 해서 인생을 잘 알고 행.복을 잘 알 까요?\n\n          오히려 인생이 더 어렵고 무언가를 잃어가는 것만 같은 기분이 들기도 한다.\n\n          그런 우리에게 힐링을 주는 책.\n\n          힐링이 필요한 이 시대를 살아가는 모든 이들에게 추천.\n        ")]),_c('p',{staticClass:"mb-0"},[_vm._v("\n          인생은 나의 것! 나의 길은 내가 정하는 것!\n\n          남의 눈치 보지 않고 내가 원하는 대로\n\n          살아가는 사람만이 진정한 삶을 즐길 수 있다\n\n          남의 눈만 신경쓰지 않아도\n\n          내 인생은 두 배로 자유로울 것만 같다\n\n          너무 남을 생각하느라 나를 버려둔 것은 아닐까\n\n          숨 막히게 바쁜 이 세상에서\n\n          나를 좀 더 보살펴 주고 나를 사랑해주자\n\n          내가 나를 사랑하지 않는다면 인생은 아무런 의미가 없다\n\n          오늘부터 나를 돌보고 나를 사랑하는 연습을 더 많이 해야겠다\n        ")])])],1)],1),_c('v-tab-item',[_c('v-card',{staticStyle:{"background":"coral"},attrs:{"flat":""}},[_c('v-card-title',{staticClass:"headline"},[_vm._v("모든 순간이 너였다")]),_c('v-card-text',[_c('p',[_vm._v("\n          생각 해보면 뻔하고 당연한 말이지만\n\n          글을 아주 따스하게 적는 재주가 있는 것 같다\n\n          짧은 글 속에서 한 편의 현대 동화를 본 기분이랄까?\n\n          정말로 모두 잠시 뿐이었으면 좋겠다\n\n          그냥 다 지나가버리고 새로운 날이 왔으면..\n\n          공감이 되면서 위로를 얻는 글이라서 좋다\n        ")]),_c('p',{staticClass:"mb-0"},[_vm._v("\n          세상에 설명이 불가능한 일들이 많다는 말이\n\n          왜 이렇게 공감이 되는 것일까..\n\n          살면서 그냥 이유 없이 슬픈 날이 있고\n\n          내일이 된다고 괜찮아지는 것이 아니다.\n\n          삶이라는 것이 답이 없고 슬픈 것에도 이유가\n\n          없을 때가 있는 것이다.. 그래도 웃어야지 하면서\n\n          웃음을 잃지 않기 위해 노력하며 살아가는 것 같다.\n\n          어쩌면 우리는 모두 대단한 사람일지도 모르겠다.\n\n          모든 순간이 너였다 글귀도 좋지만\n\n          무엇보다도 제목이 너무 예쁜 것 같다\n\n          내가 살면서 겼는 상황과 순.간이 '너'였다..\n\n          내 삶이 사랑하는 누군가에 초점이 맞춰져 있고\n\n          삶에서 일어나는 일들이 누군가에 초점이 맞춰져 있고\n\n          함께 행복하고 함께 아름다운 삶을 만드는\n\n          그런 감동적인 말들을 전하는 책이다.\n        ")])])],1)],1)],1)],1),_c('v-carousel',{attrs:{"cycle":"","height":"450","hide-delimiter-background":"","show-arrows-on-hover":""}},_vm._l((_vm.items),function(item,i){return _c('v-carousel-item',{key:i,attrs:{"src":item.src}},[_c('v-sheet',{attrs:{"color":_vm.colors[i],"height":"10%"}},[_c('v-layout',{attrs:{"align-center":"","fill-height":"","justify-center":""}},[_c('div',{staticClass:"display-1"},[_vm._v("Today's Book  \""+_vm._s(_vm.slides[i])+"\"")])])],1)],1)}),1),_c('HelloWorld'),_c('AddHome',{attrs:{"nameOfChild2":_vm.name}}),_c('Hello')],1)}
